.container__todoList {
  width: 100%;
}

.todoItem {
  text-decoration: none;
  margin: 20px;
  border: 1px solid #fff;
  background-color: #ccf7e3;
  padding: 10px;
  border-radius: 10px;
  display: flex;
  justify-content: space-between;
}

.container__addItem {
  margin: 20px;
}

.form__item {
  display: flex;
  margin-bottom: 40px;
}

.form__item > input[type="text"] {
  width: 100%;
  padding: 10px;
  border-radius: 5px;
  border: none;
}

.btn_add {
  color: white;
  background: #0b1374;
  border-radius: 10%;
  margin: 0 0 0 15px;
  padding: 10px;
  border: none;
}

.todoBtn {
  display: flex;
}

.remove-btn {
  background-color: red;
  color: #fff;
  border-radius: 45%;
  cursor: pointer;
}

.iconOk {
  width: 21px;
  margin-right: 10px;
}

.container_countTasks {
  display: flex;
  margin: 35px 20px;
}

.lbl_count {
  width: 50%;
  color: #fff;
  padding: 15px;
}

.lbl_count.incomplete {
  display: flex;
  justify-content: flex-end;
  background: red;
}

.lbl_count.complete {
  background: green;
}
